import { DownloadModel } from './../downloads.model';
import { Globals } from './../../Globals';
import { Component, OnInit } from '@angular/core';
import { FileSelectDirective, FileUploader} from 'ng2-file-upload';
import { DownloadsService } from './../downloads.service';
import {saveAs} from 'file-saver';
import { FormBuilder, FormGroup, Validators, NgForm } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';




@Component({
  selector: 'app-payslips',
  templateUrl: './payslips.component.html',
  styleUrls: ['./payslips.component.css']
})
export class PayslipsComponent implements OnInit {

  attachmentList: any = [];
  fileData: DownloadModel[];
  PayslipFormgroup: FormGroup;
  ippisno: any;


constructor(private _fileService: DownloadsService,  private globals: Globals, private _formBuilder: FormBuilder,
  private toastr: ToastrService) {
    this.attachmentList.push(JSON.parse('"Text"'));
}


download(index) {
  const filename = this.fileData[0].originalname;
  console.log(filename);
  this._fileService.downloadFilePayslip(filename)
  .subscribe(
      data => saveAs(data, filename),
      error => console.error(error)
  );
}

ViewPayslips() {
this._fileService.GetListofAllPayslips(this.PayslipFormgroup.value).subscribe(result => {
   this.fileData = result;
},
error => {
  this.showError();
});
}

showSuccess() {
  this.toastr.success('New Staff Details Added Succesully');
}
showError() {
  this.toastr.error('File Not available for download');
}

ngOnInit() {
this.ippisno = localStorage.getItem('ippisno');
this.PayslipFormgroup = this._formBuilder.group({
  PayslipYear: ['', Validators.required],
  PayslipMonth: ['', Validators.required],
  PayslipIPPIS: ['', Validators.required]
});

}

}
