import { Globals } from './../../Globals';
import { Component, OnInit } from '@angular/core';
import { FileSelectDirective, FileUploader} from 'ng2-file-upload';
import { DownloadsService } from './../downloads.service';
import {saveAs} from 'file-saver';


@Component({
  selector: 'app-memos',
  templateUrl: './memos.component.html',
  styleUrls: ['./memos.component.css']
})
export class MemosComponent implements OnInit {
  readonly uri = 'http://' + this.globals.ipaddress + ':3000/uploads/upload';



    attachmentList: any = [];


  constructor(private _fileService: DownloadsService,  private globals: Globals) {

      this.attachmentList.push(JSON.parse('"Text"'));
  }


  download(index) {
    const filename = this.attachmentList[index].originalname;
    console.log(filename);
    this._fileService.downloadFile(filename)
    .subscribe(
        data => saveAs(data, filename),
        error => console.error(error)
    );
}

  ngOnInit() {
this._fileService.GetListofAllMemos().subscribe(result => {
 this.attachmentList = result;

}, error => console.error(error));
}

}
