import { MemosComponent } from './memos/memos.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PayslipsComponent } from './payslips/payslips.component';

const DownloadsRoutes: Routes = [
    { path: 'memos', component:  MemosComponent , data: { animation: 'buttons' }},
    { path: 'payslips', component:  PayslipsComponent , data: { animation: 'buttons' }},
    { path: '', redirectTo: '/auth/dashboard', pathMatch: 'full'}
];

@NgModule({
    imports: [
        RouterModule.forChild(DownloadsRoutes)
    ],
    exports: [
        RouterModule
    ]
})

export class DownloadsRouterModule {}
